body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.alignleft {
  text-align: left;
}

table.fullWidth { 
  width: 100%;
}

table.yellowTable {
  border-collapse: collapse;
}

table.yellowTable tbody tr td {
  border: 0;
  background-color: #f1f6d6;
}

.noBottomMargin {
  margin-bottom: 0px;
}

.modal {
  width: 500px;
  background: #f1f6d6;
  border: 1px solid #ccc;
  transition: 1.1s ease-out;
  box-shadow: 6px 6px 6px 6px rgba(0, 0, 0, 0.2);
  filter: blur(0);
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  margin: 0 auto;
}

.modal .red {
  background: black;
  color: red;
}

.modal .blue {
  background: #5764ae;
  color: red;
}

.ql-editor {
  background: white;
}

.ql-editor p {
  text-align: left;
  margin: 10px 0 !important;
}

.ql-toolbar {
  background: white;
}

.modal .off {
  opacity: 0;
  visibility: hidden;
  filter: blur(8px);
  transform: scale(0.33);
  box-shadow: 1rem 0 0 rgba(black, 0.2);
  offset-rotate: 0deg;
  offset-path: path("M 250,100 S -300,500 -700,-200");
  offset-distance: 100%;
}

.modal h2 {
  border-bottom: 1px solid #ccc;
  padding: 1rem;
  margin: 0;
}

.modal .content {
  padding: 1rem;
}

.modal .actions {
  border-top: 1px solid #ccc;
  background: #eee;
  padding: 0.5rem 1rem;
  text-align: center;
}

.modal .actions button {
  border: 0;
  background: #d4d4eb;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  font-size: 0.8rem;
  line-height: 1;
}

.modal #centered-toggle-button {
  position: absolute;
}

input[type=email] {
  width: 350px;
}

figure {
  margin: 0 !important;
}