nav ul {
  list-style-type: none;
  margin: 16px auto;
  padding: 0;
  width: 460px;    /* Due to scaling on text, this is a customized amount for each set of menus  */
}

nav {
  margin: 0 auto;
}

nav ul li {
  display: inline;
  width: 100px;     /* Items are scaled to the text within */
  padding: 8px 16px;
  text-align: center;
  background-color: #d4d4eb;
}

nav ul li:hover {
  background-color: white;
  cursor: pointer;
}

table {
  margin: 0 auto;
  border-collapse: collapse;
}

table tr td,table tr th {
  border: 1px solid #888888;
  padding: 6px;
  text-align: center;
}

table.noborder tr td,table.noborder tr th {
  border: 0;
}

textarea {
  width: 200px;
  height: 300px;
}

main p {
  text-align: center;
  max-width: 800px;
  margin-left: auto !important;
  margin-right: auto !important;
}

img[alt='loading'] {
  width: 64px;
  height: 64px;
}

span.action_required input[type=text] {
  width: 80px;
}

tr.greyedOut td {
  background-color: #AAAAAA;
}

h1,h2,h3,h4 {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.max-width-1000 {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.smallEmail {
  font-size: 11px;
  font-weight: normal;
}